
<template>

   <div id='editModal' class="edit-modal">
         <button id='close' @click="closeModal" class="btn  btn-close">×</button>

      <div v-for="(value, name) in TempTimeDefault" :key='name'>
         <h2 class="edit-block__title">{{ name[0].toUpperCase() + name.slice(1) }} Time</h2>
        <div class="edit-block">
           <button @click="plus(name)" class="edit-block__btn plus">+</button>

            <input  
							class="edit-block__time min" 
							maxlength="2" 
							@blur='checkValue($event, name, "m")' 
							@focus='resetValue($event, name, "m")'  
							:value="value.m < 10 ? '0' + value.m :  value.m">

							<span class="sep">:</span>

            <input  
							class="edit-block__time sec" 
							maxlength="2" 
							@blur='checkValue($event, name, "s")' 
							@focus='resetValue($event, name, "s")' 
							:value="value.s < 10 ? '0' + value.s :  value.s">

            <button @click="minus(name)" class="edit-block__btn minus">-</button>
        </div>
      </div>

      <button id='saveData' @click="save" class="btn btn-save">Save</button>
   </div>

</template>

<script>
   
   function clone(obj) {
    if (null == obj || "object" != typeof obj) return obj;
    var copy = obj.constructor();
    for (var attr in obj) {
        if (obj.hasOwnProperty(attr)) copy[attr] = obj[attr];
    }
    return copy;
}



export default {

    props: {
        timeDefault: null
    },
    data() {
        return {
            TempTimeDefault: {
                work: {
                    m: Math.floor(this.timeDefault.work / 60),
                    s: Math.floor(this.timeDefault.work % 60)
                },
                rest: {
                    m: Math.floor(this.timeDefault.rest / 60),
                    s: Math.floor(this.timeDefault.rest % 60)
                },
                coffee: {
                    m: Math.floor(this.timeDefault.coffee / 60),
                    s: Math.floor(this.timeDefault.coffee % 60)
                }
            }
        }
    },

    methods: {
        
        plus(type) {
            if (this.TempTimeDefault[type].m < 99) {
                this.TempTimeDefault[type].m++;
            }
        },
        minus(type) {
            if (this.TempTimeDefault[type].m > 0) {
                this.TempTimeDefault[type].m--;
            }
				},
				
				resetValue(event, type, secOrMin){
					// При фокусе значение сбрасывается
					setTimeout(()=>{event.target.value = '';}, 1);
					// this.TempTimeDefault[type][secOrMin] = 0;
					
				},
				
				checkValue(event, type, secOrMin){
					console.log(secOrMin)
					// Проверяем введенное значение
					let value = event.target.value;
					if (value !== ''){
						value = parseInt(value);
						if (isNaN(value)){
							setTimeout(()=>{event.target.value = '00';}, 1);
							this.TempTimeDefault[type][secOrMin] = 0;
						}else{
							if (value < 10){
								setTimeout(()=>{event.target.value = '0' + value;}, 1);	
								this.TempTimeDefault[type][secOrMin] = value;
							}else{
								if (secOrMin === "s"){
									if (value < 60){
										this.TempTimeDefault[type][secOrMin] = value;
										setTimeout(()=>{event.target.value = value;}, 1);	
									}else{
										this.TempTimeDefault[type][secOrMin] = 59;
										setTimeout(()=>{event.target.value = 59;}, 1);	
									}
								}else if (secOrMin === 'm'){
									if (value < 100){
										this.TempTimeDefault[type][secOrMin] = value;
										setTimeout(()=>{event.target.value = value;}, 1);	
									}else{
										this.TempTimeDefault[type][secOrMin] = 99;
										setTimeout(()=>{event.target.value = 99;}, 1);	
									}
								}else{
									setTimeout(()=>{event.target.value = '00';}, 1);
									this.TempTimeDefault[type][secOrMin] = 0;
								}
							}
						}

					}else{
						setTimeout(()=>{
							value = this.TempTimeDefault[type][secOrMin];
							event.target.value = value<10? '0'+ value : value;}, 1);
						// this.TempTimeDefault[type][secOrMin] = 0;
					}
				},



        closeModal() {
            this.$emit('close')
        },

        save() {
            this.$emit('save', {
							work: this.TempTimeDefault.work.m*60 +this.TempTimeDefault.work.s,
							rest: this.TempTimeDefault.rest.m*60 +this.TempTimeDefault.rest.s,
							coffee: this.TempTimeDefault.coffee.m*60 +this.TempTimeDefault.coffee.s
						})
        }
    },

}
</script>

