<template>
<span class='progress'>
    <span class="progress-bar" id="progressBar" :style="width"></span>
</span>
</template>

<script>

   export default {
    props: {
      currentTime: null,
      state: null,
      timeDefault: null,
      colors: null
    },
    computed: {
      width () {
          let max = this.timeDefault[this.state];
          return {
            backgroundColor: this.colors[this.state],
            width: 100 - (this.currentTime / max * 100) + '%'
            
          }
      },
    },
    methods: {
    }
  }

</script>

