<script>

import * as workerTimers from 'worker-timers';
import Vue from 'vue'; 
import ProgressBar from './components/ProgressBar.vue';
import TimeEdit from './components/TimeEdit.vue';

Vue.component('progress-bar', ProgressBar);
Vue.component('time-edit', TimeEdit);

function changeFavicon(src) {
  var link = document.createElement('link'),
      oldLink = document.getElementById('dynamic-favicon');
  link.id = 'dynamic-favicon';
  link.rel = 'shortcut icon';
  link.href = src;
  if (oldLink) {
   document.head.removeChild(oldLink);
  }
  document.head.appendChild(link);
 }

class Storage {
  constructor (key) {
    this._storageKey = key;
  }

  load (){
    const string = localStorage.getItem(this._storageKey);
    const data = JSON.parse(string);
    return data ? data : false;
  }

  save (data) {
    const string = JSON.stringify(data);
    localStorage.setItem(this._storageKey, string);
  }
}

const pomodoroLocal = new Storage('vue-pomodoro-timer');

const timeDefault = pomodoroLocal.load() ? pomodoroLocal.load() :{
  work: 25*60,
  rest: 5*60,
  coffee: 15*60
};

export default {
  name: 'app',
    data() {
      return {
        timeDefault: timeDefault,
          states: {
              work: false,
              rest: false,
              coffee: false
          },
          colors: {
            work: "rgb(210, 73, 74)",
            rest: "rgb(70, 161, 47)",
            coffee: "rgb(17, 177, 206)",
          },
          stateContent: '',
          timerId: null,
          pomodoroCounter: 0,
          current: 0,
          isWorking: false,
          editIsOpen: false
      }
    },
    created: function() {
        this.setState('work');
    },

    computed: {
        toDisplay() {
            let m = Math.floor(this.current / 60);
            let s = Math.floor(this.current % 60);
            let result = `${m < 10 ? '0'+m : m} : ${s < 10 ? '0'+s : s}`;
            return result;
        },
    },

    methods: {
        tick() {
            this.current--;
        },

        working() {
          if (this.current> 0)
            this.current--;
          const timerID = workerTimers.setInterval(() => {
              if (this.current > 0) {
                  this.tick();
              } else {
                  this.nextState();
              }
          }, 1000);
          return timerID;
        },

        nextState() {
            if (this.currentState() === 'work') {
                if (this.pomodoroCounter !== 0 && this.pomodoroCounter % 4 === 0) {
                    this.setState('coffee');
                } else {
                    this.setState('rest');
                }
            } else {
                this.setState('work')
                this.pomodoroCounter++;
            }
        },

        setState(state) {
            Object.keys(this.states).forEach(key => {
                this.states[key] = false;
            });
            this.states[state] = true;
        },

        currentState() {
            let res = false;
            Object.keys(this.states).forEach(key => {
                if (this.states[key]) res = key;
            });
            return res;
        },
       

        toggleTimer() {
            if (!this.isWorking) {
                this.isWorking = true;
                this.timerId = this.working()
            } else {
                this.isWorking = false;
                workerTimers.clearInterval(this.timerId);
            }
        },

        changeStateContent() {
          switch (this.currentState()) {
              case 'work':
                  this.stateContent = 'Code time !';
                  break;
              case 'rest':
                  this.stateContent = 'Time to rest';
                  break;
              case 'coffee':
                  this.stateContent = 'Coffee time';
                  break;
          }
      },

      changeIcon(state) {
        if (state === 'work') 
          changeFavicon("/src/assets/img/work.ico");
        else 
          changeFavicon("/src/assets/img/rest.ico");
      },

      changeDefaultTimeValues(newTime) {
        this.editIsOpen = !this.editIsOpen;
        this.timeDefault = newTime;
        this.setState(this.currentState());
        pomodoroLocal.save({
          work: this.timeDefault.work,
          rest: this.timeDefault.rest,
          coffee: this.timeDefault.coffee,
        });
      },

    },

    watch: {
        states: {
            handler: function() {
                this.current = this.timeDefault[this.currentState()];
                this.changeStateContent();
                this.changeIcon(this.currentState());
            },
            deep: true
        },
        current: function() {
            if (this.isWorking)
              document.title = this.toDisplay;
            else
              document.title = "Pomodoro Timer";
        }
    }
}
</script>

